  //.catalog-menu &
 }  //margin-bottom: -#{$cat-item-space * 2}

.catalog-menu__item {
  $width: calc(#{round-float(100% / $cat-columns, 2) - 0.01} - #{$cat-item-space});
  width: $width;
  max-width: $width;
  flex-basis: $width;
  margin: 0 round($cat-item-space / 2) $cat-item-space * 2;
  //outline: 1px solid lightgrey

  @include media('<desk-lg') {
    $width: calc(#{round-float(100% / $cat-columns--desk-lg, 2) - 0.01} - #{$cat-item-space});
    width: $width;
    max-width: $width;
    flex-basis: $width; }

  // for ie 9
  @if $ie9 {
    .no-flexwrap & {
      display: inline-block;
      vertical-align: top; } } }

.catalog-menu__title {
