  & > a, & > span, & > strong {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    font-size: 1.4rem;
    line-height: 1.8rem;
    min-height: 3rem; //    
    text-decoration: none;
    color: $base-color;
 }    //margin-right: 1rem

  & > a {
    @include underlinedLinks($base-color); }

  & > a, & > span {

    // for ie 9
    @if $ie9 {
      .no-flexwrap & {
        display: inline-block;
        vertical-align: top; } }

    & > svg {
      display: inline-block;
      vertical-align: top;
      //width: 3rem
      //height: 3rem
      margin-right: 1rem;

      path {
        fill: black; } } } }

.catalog-menu__links {
  $angle-size: 2.0rem;
  $arrow-size: 0.6rem;

  list-style: none;
  margin: 0 0 2rem;
  padding: 0;

  .no-js &:not(.ms-rolls_initialized) {
    ul {
      display: none; } } // !   ,   ,   .

  &:last-child {
    margin-bottom: 0; }

  li {
    color: #131313;
    font-size: 0;
    line-height: 1;
    letter-spacing: -4px;
    white-space: nowrap;
    padding: 0 0 1.2rem;

    &:last-child {
      padding-bottom: 0; }

    a, & > span {
      display: inline-block;
      vertical-align: middle;
      font-size: 1.4rem;
      line-height: 1.8rem;
      letter-spacing: normal;
      white-space: normal;
      cursor: default;
      color: currentColor; }

    &.has-items {
      & > a, & > span {
        margin-right: $angle-size + 0.8; } }

    a {
      text-decoration: none;
      cursor: pointer;
      @include underlinedLinks(#131313); }

    & > em {
      position: relative;
      display: inline-block;
      vertical-align: middle;
      width: $angle-size;
      height: $angle-size;
      margin-left: -$angle-size;
      text-indent: -9999px;
      overflow: hidden;
      cursor: pointer;
      z-index: 5;
      color: currentColor;
      border: 1px solid currentColor;
      border-radius: round-float($angle-size / 2, 1);
      user-select: none;
      -webkit-tap-highlight-color: transparent;
      // transition
      $prop: transform, color;
      will-change: $prop;
      transition: {
        property: $prop;
        duration: $animation-speed; }

      &::before, &::after {
        content: "";
        display: block;
        position: absolute;
        top: 50%;
        height: 0.2rem;
        width: $arrow-size;
        background-color: currentColor;
        pointer-events: none;
        margin: -0.1rem -0.5rem; }

      &::before {
        transform: rotate(45deg);
        left: 50%; }

      &::after {
        transform: rotate(-45deg);
        right: 50%; }

      &:hover {
        color: $base-color; } } }

  ul {
    list-style: none;
    padding: 1.2rem 0 0 2rem;
    opacity: 0;
    // transition
    $prop: opacity;
    will-change: $prop;
    transition: {
      property: $prop;
      duration: $animation-speed; }

    li {
      a {
        $color: #666;
        color: $color;

        @include underlinedLinks($color); } } }

  // current
  li {
    &.current {
      & > a, & > span {
        @include font-bold;
        color: $base-color; }

      & > a {
        @include underlinedLinks($base-color); } } }

  // active
  li.active {
    & > em {
      $rotate: -0.5turn;
      transform: rotate($rotate); } }

  ul.active {
    opacity: 1; } }

// catalog list end

//=== Catalog is open ===
.main, .footer {
  // transition
  $prop: filter;
  will-change: $prop;
  transition: {
    property: $prop;
    duration: $animation-speed; } }

.catalog-is-open {
