/* btn-subs */
.btn-subs {
  position: relative;
  display: inline-block;
  vertical-align: top;
  padding: 0.7rem 1.3rem 0.7rem 3.7rem;
  margin-bottom: 2.5rem;
  font-size: 1.4rem;
  line-height: 1.8rem;
  color: #e0e0e0;
  text-decoration: none;
  border: 1px solid #494949;
  border-radius: 8px;
  // transition
  $prop: border-color;
  will-change: $prop;
  transition: {
    property: $prop;
    duration: $animation-speed; }

  &:last-child {
    margin-bottom: 0; }

  svg {
    $size: 18px;
    display: block;
    position: absolute;
    top: 50%;
    left: 1.3rem;
    height: $size;
    width: $size;
    margin-top: -#{round($size / 2)};

    path, circle {
      fill: #888;
      // transition
