  opacity: 0.3;
  // transition
  $prop: opacity;
  will-change: $prop;
  transition: {
    property: $prop;
    duration: $animation-speed; }

  &::before, &::after {
    @include pseudo();
    @include position(0, 0);
    @include size(149px, 16px); }

  &::before {
    @include pseudo();
    @include position(4px, 4px);
    background: url('data:image/png;base64,');
