    position: absolute;
    left: 0;
    top: 50%;
    width: $size;
    height: $size;
    border: 1px solid $base-color;
    border-radius: round-float($size / 2, 1);
    transform: translate(0, -50%);

    &::before {
      $size: 0.5rem 0.8rem;
      content: "";
      display: block;
      position: absolute;
      left: 50%;
      top: 50%;
      height: nth($size, 1);
      width: nth($size, 2);
      margin: -#{round-float(nth($size, 1)/2, 1) + 0.1} 0 0 -#{round-float(nth($size, 2)/2, 1)};
      border-style: solid;
      border-color: inherit;
      border-width: 0 0 1px 1px;
      transform: rotate(-45deg); } }

  // off
