/* popup with form add a product to a cart */
.p-add-form-popup {
  position: absolute;
  right: 0;
  bottom: 0;
  width: 563px;
  background-color: white;
  border: 1px solid #f5f5f5;
  box-shadow: 0 5px 11px rgba(0, 0, 0, 0.08);
  pointer-events: none;
  opacity: 0;
  z-index: 99;
  $scale: 0.85, 0.85;
  transform: scale($scale);
  transform: scale3d($scale, 1);
  transform-origin: right bottom;
  // transition
  $prop: transform, opacity;
  will-change: $prop;
  transition: {
    property: $prop;
    duration: $animation-speed;
    timing-function: ease-in-out; } }

// opened
.p-add-form-popup_opened {
  pointer-events: auto;
  opacity: 1;
  $scale: 1, 1;
  transform: scale($scale);
  transform: scale3d($scale, 1); }

.close-p-add-form-popup {
  $size: 1.8rem;
  $size-cross: 0.2rem 1rem;
  position: absolute;
  right: 1.5rem;
  top: 1.5rem;
  width: $size;
  height: $size;
  color: white;
  background-color: #d8d8d8;
  border-radius: round-float($size / 2, 1);
  text-indent: -9999px;
  overflow: hidden;
  transition: background-color $animation-speed;

  &::before, &::after {
    content: "";
    display: block;
    position: absolute;
    background-color: currentColor;
    width: nth($size-cross, 2);
    height: nth($size-cross, 1);
    top: 50%;
    left: 50%;
    margin: -#{round-float(nth($size-cross, 1) / 2, 1)} 0 0 -#{round-float(nth($size-cross, 2) / 2, 1)}; }

  &::before {
    transform: rotate(-45deg); }

  &::after {
    transform: rotate(45deg); }

  // hover
  &:hover {
    background-color: $base-color; } }

