    height: $size;

    path {
      fill: $base-color;
      // transition
      $prop: fill, stroke;
      transition: {
        property: $prop;
        duration: $animation-speed; } } }

  & > span {
    padding-top: 0.2rem;
    padding-right: 1rem; }

  .purchase-btn__buy {
    display: none; }

  .purchase-btn__in-cart {
    display: none; }

  // hover
  &:hover {
    background-color: $base-color;
    color: white;

    svg path {
      fill: white; } }

  // added
  &.added {
    svg {
      background-color: $base-color;
      border-radius: 0.1rem;
      box-shadow: 0 0 0 6px rgba($base-color, 1);

      path {
        fill: white; } }

    .purchase-btn__to-cart {
      display: none; }

    .purchase-btn__in-cart {
      display: block; } } }

// --> product installment <--
.product__installment {
  position: relative;
  display: none; }

// product option (container)
.product__options {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: stretch;
  line-height: 1;
  margin: 0 -#{nth($product-item-space, 2)};
  z-index: 1;

  &::before, &::after {
    content: "";
    display: block;
    border: solid #e6e6e6;
    position: absolute;
    z-index: -1; }

  &::before {
    left: 0.5rem;
    right: 0.5rem;
    top: 0;
    border-width: 2px 0 0; }

  &::after {
    width: 0;
    height: 100%;
    left: 50%;
    top: 0;
    border-width: 0 0 0 2px;
    margin-left: -1px; } }

.product__action,
.product__purchase {
  flex-grow: 1;
  flex-basis: 50%; }

.product__options-btn {
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center; }

// === grid view ===
//         
// favorite button in a product options
div:not([data-view="list-view"]) {
  .product__options-btn_like {
    padding: 1.8rem 1rem;
    font-size: 1.2rem;
    line-height: 1.6rem;
    text-align: center;

    svg {
      position: relative;
      left: auto;
      top: auto;
      transform: none;

      path {
        fill: transparent;
        stroke: $mark-color; } }

    & > span {
      padding-top: 0.2rem;
      padding-right: 1rem; }

    &:hover {
      background-color: $base-color;
      color: white;

      svg {
        path {
          fill: transparent;
          stroke: white; } } }

    &.added {
      svg {
        path {
          fill: $mark-color; } }

      &:hover {
        svg {
          path {
            fill: white; } } } } } }

// === list view ===
//          ( )
[data-view="list-view"] {
  $product-item-space: 3.2rem 2.6rem;
  $offer-width: 23.6rem;

  // === product ===
  .product {
    flex-direction: row; }

  // === product main ===
  .product__main {
    position: relative;
    $width: calc(100% - #{$offer-width});
    width: $width;
    min-height: 0;
    flex-basis: $width;
    //flex-grow: 1
    margin: 0;
    padding: nth($product-item-space, 1) nth($product-item-space, 2);

    & > a {
      display: inline;
      margin: 0; } }

  // === [product align left] ===
  .product__figure {
    float: left;
    width: 50%;
    padding-right: round-float(nth($product-item-space, 2) / 2, 1); }

  // === product figure ===
  .product__figure {
    height: 22.8rem;
    margin-bottom: 3rem; } //           

  // === [product align right] ===
  .product__stickers, .product__title, .product__meta, .products__features {
    float: right;
    clear: right;
    width: 50%;
    padding-left: round-float(nth($product-item-space, 2) / 2, 1); }

  // === product stickers ===
  // --> sticker wow <--
  .product__sticker_wow {
    $space: 3.5rem 1.8rem;
    right: nth($space, 2);
    top: nth($space, 1); }

  // --> stickers <--
  .product__stickers {
    position: relative;
    left: auto;
    top: auto;
    right: auto;
    margin-bottom: 1rem; }

  // === product title ===
  .product__title {
    margin-bottom: 0.6rem; }

  // === product rating ===
  .product__rate {
    margin-bottom: 1.9rem; }

  // === product meta (container) ===
  .product__meta {
    margin-bottom: 1.1rem; }

  // === products features ===
  .products__features {
    display: block; }

  // === product footer ===
  .product__footer {
    $width: $offer-width;
    width: $width;
    flex-basis: $width;
    //flex-grow: 1
    margin: 0;
    padding: nth($product-item-space, 1) 0;

    &::before {
      content: "";
      display: block;
      position: absolute;
      top: nth($product-item-space, 1);
      bottom: nth($product-item-space, 1);
      width: 0;
      border-left: 1px solid $border-color; } }

  // === product offer ===
  .product__offer {
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-end;
    height: auto;
    min-height: 10rem;
    margin: 0 0 2rem;
    padding: 0 nth($product-item-space, 2); }

  // === product stock ===
  .product__stock {
    padding: 0.5rem 0 0.5rem 3rem;

    & > i {
      $size: 2.2rem;
      width: $size;
      height: $size;
      border-radius: round-float($size / 2, 1);

      &::before {
        $size: 0.6rem 1rem;
        height: nth($size, 1);
        width: nth($size, 2);
        margin: -#{round-float(nth($size, 1)/2, 1) + 0.1} 0 0 -#{round-float(nth($size, 2)/2, 1)}; } }

    &.off {
      & > i {
        &::before {
          height: 0;
          margin-top: 0; } } } }

  // === product price ===
  .product__price {
    flex-basis: auto; }

  .product__price-old,
  .product__price-new {
    line-height: 4rem; }

  .product__price-old {
    & > strong, & > em {
      font-size: 2rem; } }

  .product__price-new {
    & > strong, & > em {
      font-size: 3.0rem; } }


  // === product option ===
  // --> product action <--
  // compare button
  .product__options-btn_compare {
    display: block; }

  // favorite (like) button
  .action-btn_like {
    position: relative; }

  // product action (container)
  .product__action {
    display: block;
    width: calc((100% - #{$offer-width}) / 2);
    position: absolute;
    left: 0;
    bottom: nth($product-item-space, 2);
    margin: 0;
    padding-right: round-float(nth($product-item-space, 2) / 2, 1);
    text-align: center;
    cursor: default; }

  .product__action-btn {
    display: inline-block;
    vertical-align: top;
    margin-left: 1.2rem;
    margin-right: 1.2rem; }

  // --> product purchase <--
  .product__purchase {
    margin-bottom: 1rem; }

  // purchase button
  .purchase-btn {
    position: relative;
    padding: 1.2rem 3.8rem;
    @include font-bold;
    @include font(1.8rem, 2.2rem);
    color: $btn-text-color;
    background-color: $btn-bg-color;
    border: none;
    border-radius: 4px;
    box-shadow: 0 1px 1px rgba(0, 0, 0, 0.4);

    .purchase-btn__to-cart {
      display: none; }

    .purchase-btn__buy {
      display: block; }

    svg {
      $size: 1.8rem;
      position: absolute;
      left: 1rem;
      top: 50%;
      height: $size;
      width: $size;
      opacity: 0;
      $translate: 10px, 0;
      transform: translate($translate);
      transform: translate3d($translate, 0);
      margin-top: -#{round-float($size / 2, 1) + 0.1};

      path {
        fill: $base-color; } }

    & > span {
      padding: 0; }

    // hover
    &:hover {
      color: #000;
      box-shadow: 0 3px 6px rgba(black, 0.3);
      top: -1px; }

    // active
    &:active {
      top: 0;
      box-shadow: none; }

    // added
    &.added {

      .purchase-btn__buy {
        display: none; }

      svg {
        background: none;
        box-shadow: none;
        border-radius: 0;
        opacity: 1;
        $translate: 0, 0;
        transform: translate($translate);
        transform: translate3d($translate, 0);
        // transition
        $prop: transform, opacity;
        will-change: $prop;
        transition: {
          property: $prop;
          duration: $animation-speed * 2;
          timing-function: cubic-bezier(0.18, 0.89, 0.32, 1.28); } } } }

  // --> product installment <--
  .product__installment {
    display: block; }

  // product options (container)
  .product__options {
    position: static;
    display: block;
    margin: 0;
    padding: 0 nth($product-item-space, 2);

    &::before, &::after {
      display: none; }

    .btn-def-alt {
      width: 100%; } } }


/* product item end */

/* product item short */
$product-short-inner-space: 2.5rem 1.5rem 1.5rem;
.product-short {
  position: relative;
  background-color: white;
  padding: $product-short-inner-space;
  cursor: default; }

// === short product stickers ===
// --> sticker wow <--
.product-short__sticker_wow {
  $space: 1.3rem 1.2rem;
  position: absolute;
  right: nth($space, 2);
  top: nth($space, 1);
  z-index: 2;

  & + .product__stickers {
    padding-right: 5.2rem; } }

// --> stickers <--
.product-short__stickers {
  $space: 0.9rem 1rem;
  display: flex;
  flex-wrap: wrap;
  position: absolute;
  left: nth($space, 2);
