  &::after {
    content: "";
    display: block;
    position: absolute;
    top: 50%;
    left: -0.1em;
    right: -0.1em;
    width: auto;
    height: 0;
    border-top: 1px solid currentColor;
    margin-top: -1px; } }

.p-short-price__old-val,
.p-short-price__old-unit {
  display: inline-block;
  vertical-align: baseline;
  font-size: 1.3rem;
  font-style: normal; }

.p-short-price__new {
  text-align: right; }

.p-short-price__new-val {
  @include font-bold;
  font-size: 2.0rem; }

.p-short-price__new-unit {
