  font-size: 1.4rem; }

/* product item short end */

/* topic-banner */
.topic-banner {
  position: relative;
  display: block;
  overflow: hidden;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  text-decoration: none;
  color: white;
  // transition
  $prop: background-size;
  will-change: $prop;
  transition: {
    property: $prop;
    duration: $animation-speed; } }

.topic-banner__title {
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  @include font-bold;
  font-size: 2.4rem;
