$gz-item-space: 18px;
$gz-columns: 4;
.gz-tiles {
  margin-bottom: $bottom-space;

  &:last-child {
    margin-bottom: 0; } }

.gz-tiles__list {
  margin-left: -#{round($gz-item-space / 2)};
  margin-right: -#{round($gz-item-space / 2)};

  &:not(.grid-completed) {
    overflow: hidden;
    white-space: nowrap; } }

.gz-tiles__item {
  display: inline-block;
  vertical-align: top;
  width: round-float(100% / $gz-columns, 2);
  padding: 0 round($gz-item-space / 2) $gz-item-space;
  color: white;
  text-decoration: none; }

// gz-tiles-elem
$gz-tiles-elem-space: 3.5rem 3.8rem;
.gz-tiles-elem {
  height: 0;
  display: block;
  position: relative;
  overflow: hidden; }

.gz-tiles-elem__bg {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: #080f1f;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  // transition
  $prop: transform;
  will-change: $prop;
  transition: {
    property: $prop;
    duration: 1.5s;
    timing-function: ease-in-out; } }

.gz-tiles-elem__title {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  padding: $gz-tiles-elem-space;
  @include font-light;
  font-size: 2.4rem;
  line-height: 2.5rem;
  color: #c8c8c8;

  [data-align~="top"] & {
    align-items: flex-start; }

  [data-align~="middle"] & {
    align-items: center;
    justify-content: flex-start; }

  [data-align~="bottom"] & {
    align-items: flex-end;
    justify-content: flex-start; }

  [data-align~="center"] & {
    justify-content: center;
    text-align: center; }

  [data-align~="left"] & {
    justify-content: flex-start; }

  [data-align~="right"] & {
    justify-content: flex-end;
    text-align: right; } }

// gz-tiles-elem (states)
.gz-tiles-elem {
  // hover
  &:hover {
    .gz-tiles-elem__bg {
      transition-duration: 1.0s;
      $scale: 1.1, 1.1;
      $translate: 0, 0;
      transform: scale($scale) translate($translate);
      transform: scale3d($scale, 1) translate3d($translate, 0); } }

  &[data-align~="top"][data-align~="left"]:hover {
    .gz-tiles-elem__bg {
      $scale: 1.1, 1.1;
      $translate: -4%, -4%;
      transform: scale($scale) translate($translate);
      transform: scale3d($scale, 1) translate3d($translate, 0); } }

  &[data-align~="top"][data-align~="right"]:hover {
    .gz-tiles-elem__bg {
      $scale: 1.1, 1.1;
      $translate: 4%, -4%;
      transform: scale($scale) translate($translate);
      transform: scale3d($scale, 1) translate3d($translate, 0); } }

  &[data-align~="middle"][data-align~="left"]:hover {
    .gz-tiles-elem__bg {
      $scale: 1.1, 1.1;
      $translate: -4%, 0;
      transform: scale($scale) translate($translate);
      transform: scale3d($scale, 1) translate3d($translate, 0); } }

  &[data-align~="middle"][data-align~="right"]:hover {
    .gz-tiles-elem__bg {
      $scale: 1.1, 1.1;
      $translate: 4%, 0;
      transform: scale($scale) translate($translate);
      transform: scale3d($scale, 1) translate3d($translate, 0); } } }



/* quick-links end */

/* articles main previews */
