/* view-switcher */
.view-switcher {
  $size: 2.8rem;
  $space: 1rem;

  position: relative;
  width: $size * 2 + $space;
  @include clear;

  &::before {
    content: "";
    display: block;
    position: absolute;
    left: 0;
    top: 0;
    width: $size;
    height: $size;
    background-color: $base-color;
    border-radius: 3px;
    // transition
    $prop: transform;
    will-change: $prop;
    transition: {
      property: $prop;
      duration: $animation-speed; } }

  &[data-view="grid-view"] {
    &::before {
      $translate: 0, 0;
      transform: translate($translate);
      transform: translate3d($translate, 0); } }

  &[data-view="list-view"] {
    &::before {
      $translate: $size + $space, 0;
      transform: translate($translate);
      transform: translate3d($translate, 0); } }

  a {
    position: relative;
    float: left;
    display: block;
    width: $size;
    height: $size;
    text-decoration: none;
    color: #b0a1f0;
    margin-left: $space;
    border-radius: 3px;
    // transition
    $prop: color;
    transition: {
      property: $prop;
      duration: $animation-speed; }

    &:first-child {
      margin-left: 0; }

    svg {
      display: inline-block;
      vertical-align: top;
      width: $size;
      height: $size; }

    path {
      fill: currentColor; }

    & > span {
      @include hide-text; }

    // hover
    .no-touchevents &:not(.active) {
      &:hover, &:focus {
        color: $base-color; } }

    // active
    &.active {
      cursor: default; } } }

/* view-switcher end */

/* options panel */
