/* options panel end */

/* p-sorting */
.p-sorting {
  $angle-size: 2.0rem;
  $arrow-size: 0.7rem;

  list-style: none;
  margin: 0;

  &:last-child {
    margin-bottom: 0; }

  a {
    position: relative;
    display: inline-block;
    vertical-align: top;
    padding: 2px 10px 2px 25px;
    margin-right: 10px;
    color: #777;
    font-size: 1.4rem;
    line-height: 2.0rem;
    text-decoration: none;
    border-radius: 4px;

    & > em {
      position: absolute;
      left: 3px;
      top: 50%;
      width: $angle-size;
      height: $angle-size;
      margin: -#{round-float($angle-size / 2, 1)} 0;
      text-indent: -9999px;
      overflow: hidden;
      cursor: pointer;
      z-index: 5;
      border-radius: 0.4rem;
      user-select: none;
      -webkit-tap-highlight-color: transparent;
      // transition
      $prop: transform;
      will-change: $prop;
      transition: {
        property: $prop;
        duration: $animation-speed; }

      &::before, &::after {
        content: "";
        display: block;
        position: absolute;
        top: 50%;
        height: 0.2rem;
        width: $arrow-size;
        background-color: $base-color;
        pointer-events: none;
        margin: -0.1rem -0.5rem;
        // transition
        $prop: background-color;
        transition: {
          property: $prop;
          duration: $animation-speed; } }

      &::before {
        transform: rotate(45deg);
        left: 50%; }

      &::after {
        transform: rotate(-45deg);
        right: 50%; } }

    // click
    &:active {
      top: 1px; }

    // active
    &.active {
      $color: white;
      background-color: $base-color;
      color: $color;

      & > em {
        &::before, &::after {
          background-color: $color; } } }

    // order-top
    &.order-top > em {
      $rotate: -0.5turn;
      transform: rotate($rotate); } } }

/* p-sorting end */

/* filters tags */
.m-tags {
  display: none;

  .filters-results-show & {
    display: block; } }

.p-filters-tags {
  //position: sticky
  //top: 0
  padding: 0 0 2rem;
  position: relative;
  line-height: 2.0rem;
  margin-left: -1rem;
  -webkit-tap-highlight-color: transparent; }

.p-filters-tags__item {
  $this-icon-lines-width: 10px;
  $this-icon-lines-height: 1px;
  //display: inline
  display: inline-block;
  vertical-align: top;
  position: relative;
  @include font(1.4rem, 2.0rem);
  color: $base-color;
  white-space: nowrap;
  cursor: pointer;
  margin-left: 1rem;
  user-select: none;
  -webkit-tap-highlight-color: transparent;

  & > span {
    margin-right: $this-icon-lines-width + 3;
    white-space: normal;
    text-decoration: underline; }

  & > i {
    position: relative;
    display: inline-block;
    vertical-align: top;
    opacity: 0;
    @include size(#{round($this-icon-lines-width)});
    margin-left: -$this-icon-lines-width;
    top: 6px;
    @include font(0, 0);
    // transition
    $prop: opacity;
    will-change: $prop;
    transition: {
      property: $prop;
      duration: $animation-speed; }

    &::before, &::after {
      @include pseudo();
      left: 50%;
      top: 50%;
      backface-visibility: hidden;
      @include size($this-icon-lines-width, $this-icon-lines-height);
      margin: -#{round($this-icon-lines-height*0.5)} 0 0 -#{round($this-icon-lines-width*0.5)};
      background-color: currentColor; }

    &::before {
      transform: rotate(-45deg); }

    &::after {
      transform: rotate(45deg); } }

  .no-touchevents &:hover {
    //& > span
    //text-decoration: none

    & > i {
      opacity: 1; } } }

/* filters tags end */

/* btn-filters-close */
.btn-filters-close {
  display: block;
  position: relative;
  background-color: $bg-color;
  @include font-bold;
  @include font(14px, $header-height--sm);
