/* btn-filters-close end */

/* filters */
.p-filters-list {
  position: relative;
  padding: 0 2rem; }

.p-filters-item {
  position: relative;
  border: solid $border-color;
  padding: 1.9rem 0;
  border-width: 0 0 1px; }

// switcher
.p-filters-select {
  position: relative;
  cursor: pointer;
  padding: nth($p-filters-space, 1) nth($p-filters-space, 2) nth($p-filters-space, 1) 3.3rem;
  line-height: 1;
  user-select: none;
  -webkit-tap-highlight-color: transparent; }

// title
.p-filters-title {
  @include font-normal;
  @include font(1.8rem, 2.6rem);
  color: #000; }

// angle
.p-filters-angle {
  position: absolute;
  left: nth($p-filters-space, 2);
  top: 50%;
  @include size($p-filters-angle-size);
  margin-top: -#{round-float($p-filters-angle-size / 2, 1)};
  border: 1px solid $border-color;
  border-radius: round-float($p-filters-angle-size / 2, 1);
  user-select: none;
  -webkit-tap-highlight-color: transparent;
  // transition
  $prop: transform;
  will-change: $prop;
  transition: {
    property: $prop;
    duration: $animation-speed; }

  & > i {
    display: block;
    position: absolute;
    left: 50%;
    top: 50%;
    height: $p-filters-angle-arrow-size;
    width: $p-filters-angle-arrow-size;
    margin: {}
    border: solid $base-color;
    border-width: 0 0 0.2rem 0.2rem;
    margin: -0.5rem -0.4rem;
    transform: rotate(-45deg); } }

// counter
.p-filters-selected {
  opacity: 0;
  pointer-events: none;
  //===================
  $param: transform, opacity;
  will-change: $param;
  transition: {
    property: $param; }

  &::before {
    content: "("; }

  &::after {
    content: ")"; }

  &.filters-selected-show {
    opacity: 1;
    transition: {
      duration: $animation-speed; } } }


// drop
.p-filters-drop {
  position: relative;
  display: none; }

.p-filters-drop-list {
  $item-space: 1.4rem;
  overflow-x: hidden;
  overflow-y: auto;
  max-height: 33rem;

  &, .#{$user-content} & {
    list-style: none;
    margin: 0;
    padding: 0 nth($p-filters-space, 2) nth($p-filters-space, 1) - $item-space;

    li {
      position: relative;
      padding-left: 0;
      margin: 0 0 $item-space;

      &::before {
        content: none; }

      &:first-child {
        &:before {
          content: none; } } } }

  label {
    display: block; }

  // item float
  &.item-float {
    @include margin-horizontal(-0.6rem);
    @include clear;

    li {
      float: left;
      margin-left: 0.6rem;
      line-height: 1; } }

  // group of form's elements
  .form-row {
    $space: 2rem;
    margin-left: -#{round-float($space / 2, 1)};
    margin-right: -#{round-float($space / 2, 1)};

    .input-wrap {
      margin-left: round-float($space / 2, 1);
      margin-right: round-float($space / 2, 1);
      width: calc(100% / 2 - #{$space}); } } }

label.checkbox_value {
  & > span {
    display: inline-block;
    vertical-align: top;
    height: 3.2rem;
    border-radius: 1.6rem;
    text-align: center;
    padding: 0.5rem 1rem;
    font-size: 1.4rem;
    line-height: 2.0rem;
    border: 1px solid $border-color;
    background-color: white;
    // transition
    $prop: color, background-color, border-color, box-shadow;
    will-change: $prop;
    transition: {
      property: $prop;
      duration: $animation-speed; }

    &::before, &::after {
      content: none; } }

  input[type="checkbox"]:checked ~ span {
    background-color: $base-color;
    border-color: $base-color;
    color: white; }

  .no-touchevents &:hover > span {
    border-color: $base-color;
    box-shadow: inset 0 0 0 1px $base-color; } }

// ===STATES===
// hover
.p-filters-select {
  .no-touchevents &:hover {
    .p-filters-title {
      color: $base-color; } } }

// has checked filters
.p-filters-item.p-filters-on {
  .p-filters-select {
    .p-filters-title {
      @include font-bold;
      color: $base-color; } } }

// drop open
.p-filters-item.p-filters-is-open {

  .p-filters-angle {
    transform: rotate(0.5turn); }

  .p-filters-drop {
    display: block; } }

/* filters end */

/* filters-results */
.p-filters-results {
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: absolute;
  bottom: 0;
  height: 50px;
  width: $p-filters-width;
  background-color: $base-color-alt;
  color: #000;
  z-index: 1;
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  transition: {
    property: opacity, visibility;
    duration: $animation-speed;
    delay: 0.5s; }

  @include media('<mob-lg') {
    width: 100%; }

  .filters-results-show & {
    opacity: 1;
    visibility: visible;
    pointer-events: auto;
    transition: {
      delay: 0s; } }

  .filters-result-fixed & {
    height: 75px;
    padding-bottom: 25px;
    position: fixed;
    bottom: -1px; }

  &, .filters-result-fixed & {
    @include media('<tab-lg') {
      z-index: 10100;
      position: fixed;
      left: 0;
      bottom: 0; } }

  .results-loading {
    @include size(calc(100% + 2px));
    position: absolute;
    left: -1px;
    top: -1px;
    @include font-bold;
    @include font(1.4rem, 50px);
    color: white;
    text-align: center;
    z-index: 5;
    background-color: rgba(153, 153, 153, 1);
    opacity: 0;
    visibility: hidden;
    transition: {
      property: opacity, visibility;
      duration: $animation-speed; }

    .js-loading & {
      opacity: 1;
      visibility: visible; } } }

// selected
.p-filters-results__selected {
  padding: 0.5rem 1rem;
  flex-grow: 1; }

.p-filters-results__selected__align {
  text-align: left; }

.p-filters-results__selected__label {
  @include font-light;
  @include font(1.2rem, 1.6rem);
  text-transform: uppercase; }

.p-filters-results__selected__count {
  @include font-bold;
  @include font(1.4rem, round-float(16/14, 2));
  letter-spacing: normal;

  .cur {
    color: $base-color; } }

// apply btn
.p-filters-results__apply {
  padding: 0.5rem 1rem;
  flex-grow: 1;

  .btn-filters-apply {
    display: block;
    padding: 1.2rem 1rem 1rem;
    @include font-bold;
    font-size: 1.4rem;
    line-height: 1.8rem;
    text-transform: uppercase;
    background-color: $base-color;
    border-radius: 2rem;
    color: white;
    z-index: 1;
    // transition
    $prop: background-color, color;
    //will-change: $prop
    transition: {
      property: $prop;
      duration: $animation-speed; }

    // hover
    .no-touchevents &:hover {
      color: $header-bg;
      background-color: white; } } }

// btn-clear-filters
.btn-clear-filters {
  @include size(5rem);
  padding: 0;
  background-color: #ff622e;
  border: none;
  color: white;
  // transition
  $prop: background-color, color;
  //will-change: $prop
  transition: {
    property: $prop;
    duration: $animation-speed; }

  svg {
    @include size(36px); }

  path {
    fill: currentColor; }

  // hover
  .no-touchevents &:hover {
    background-color: #ff2b0a; }

  // focus
  &:disabled {
    background-color: $btn-disabled-color !important;
    color: white !important; } }

.loader-cover {
  @include size(100%);
  position: absolute;
  left: 0;
  top: 0;
  z-index: 5;
  background-color: rgba(255, 255, 255, 0.50);
  opacity: 0;
  visibility: hidden;
  transition: {
    property: opacity, visibility;
    duration: $animation-speed; }

  @include media('<tab-lg') {
    position: fixed;
    z-index: 10100; }

  .js-loading & {
    opacity: 1;
    visibility: visible; }

  &::before {
    $loader-size: 120px;
    // ==================
    content: "";
    display: block;
    position: fixed;
    left: 50%;
    top: 50%;
    @include size($loader-size);
    transform: translate(-50%, -50%);
    background-image: url(../../img/preloader.svg);
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain; } }

/* filters-results end */

/* toggle filters for devices */
.m-aside, .p-filters-results {
  @include media('<tab-lg') {
    //opacity: 0
    //visibility: hidden
    will-change: transform;
    transform: translate(-100%, 0);
    transform: translateX(-100%);
    transition: {
      property: transform;
      duration: $animation-speed; }

    &.active {
      transform: translate(0, 0);
      transform: translateX(0); } } }

/* toggle filters for devices end */

/* collections */
//$collections-space: 2.2rem 4.7rem
$collections-space: $section-inner-space;
$collections-title-width: 12.5rem;
.collections {
  position: relative;
  padding: $collections-space;
  display: flex;
  align-items: center;
  background-color: white;

  &::before {
    content: "";
    display: block;
    position: absolute;
    top: nth($collections-space, 1) + 0.5;
    bottom: nth($collections-space, 1) + 0.5;
    left: $collections-title-width + nth($collections-space, 2);
    width: 0;
    border-left: 1px solid $border-color; } }

.collections__title {
  width: $collections-title-width;
  flex-basis: $collections-title-width;
  @include font-bold;
  color: #000;
  font-size: 2.1rem;
  line-height: 2.2rem;
  margin: 0; }

.collections__content {
  flex-grow: 1;
  flex-basis: calc(100% - #{$collections-title-width + nth($collections-space, 2)});
  padding: 0 0 0 3.7rem;

  // for ie 9
  @if $ie9 {
    .no-flexwrap & {
      margin-left: $collections-title-width; } } }

.collections__thumbs {
  display: flex;
  flex-wrap: wrap;
  font-size: 1.4rem;
  line-height: 2.0rem;
  margin-bottom: 1rem;

  &:last-child {
    margin-bottom: 0; }

  a {
    color: #777;
    text-decoration: none;
    margin: 0.5rem 4rem 0.5rem 0;

    // for ie 9
    @if $ie9 {
      .no-flexwrap & {
        display: inline-block;
        vertical-align: top; } }

    // touch
    &.touchevents {
      user-select: none;
      -webkit-tap-highlight-color: transparent; }

    @include underlinedAnchor(#777);

    // active
    &.tabs-active {
      color: #000;
      //+font-bold

      & > span {
        border-color: transparent; } } } }

/* collections end */

/* float-list */
.float-list-wrap {
  overflow: hidden; }

.float-list {
  font-size: 1.4rem;
  line-height: 2.0rem;
  cursor: default;

  &, .#{$user-content} & {
    list-style: none;
    margin: 0 0 2rem;
    padding: 0;

    &:last-child {
      margin-bottom: 0; }

    li {
      margin: 1.2rem 1.2rem 0 0;
      padding: 0;

      &:before {
        content: none; } } }

  li {
    display: inline-block;
    vertical-align: top;
    color: $base-color; }

  a {
    color: #777;
    text-decoration: none;
    cursor: pointer;

    @include underlinedLinks(#777); }

  li.current {
    a {
      color: $base-color;

      @include underlinedLinks($base-color); } } }

/* float-list end */

/* product card */
$card-cont-width: 520px; // 520px (43.8%)   1280px

.p-card {
  background-color: white;
  padding: 4.8rem nth($section-inner-space, 2) 4.2rem;
  margin-bottom: 4.8rem;
  @include clear;

  &:last-child {
    margin-bottom: 0; } }

// === product card caption ===
// ============================
// title
.p-card-title {
  margin: 0; }

// heading
.p-card__caption {
  margin-bottom: 1rem; }

.p-card__caption-title {
  display: inline-block;
  vertical-align: middle;
  margin-right: 2rem; }

.p-card__caption-rate {
  display: inline-block;
  vertical-align: middle;
  margin-bottom: 1.5rem; }


// === product card main ===
// =========================
$gallery-thumbs-size: 50px;
$gallery-thumbs-item-space: 14px;
$gallery-thumbs-arrow-size: 40px 100%;
$gallery-thumbs-height: $gallery-thumbs-size * 5 + $gallery-thumbs-item-space * 4;
$gallery-thumbs-height--sm: $gallery-thumbs-size * 3 + $gallery-thumbs-item-space * 2;
.p-card__main {
  position: relative;
  float: left;
  width: calc(100% - #{$card-cont-width}); }

// === product card labels ===
.p-card-stickers {
  margin-bottom: 0.5rem;

  &:last-child {
    margin-bottom: 0; } }

// card gallery
.p-card-gallery {
  position: relative;
  //padding: 0 $gallery-thumbs-size 0 #{$gallery-thumbs-size + 30}
  padding: 0 100px 0 #{100 + $gallery-thumbs-size};
  margin-bottom: 2.4rem;

  &:last-child {
    margin-bottom: 0; }

  .js &:not(.is-loaded) {
    opacity: 0;
    pointer-events: none; } }


// card gallery images
.p-card-gallery-images {
  margin: 0 auto;
  max-width: 500px;
  min-width: $gallery-thumbs-height + nth($gallery-thumbs-arrow-size, 1) * 2;

  @include media('<desk-lg') {
    max-width: 380px; } }

.p-card-gallery-images__item {
  user-select: none;
  -webkit-tap-highlight-color: transparent;

  &::before {
    content: "";
    position: relative;
    display: block;
    overflow: hidden;
    @include ratio(1, 1); }

  img {
    position: absolute;
    left: 0;
    top: 0;
    height: 100%;
    width: 100%;
    max-width: none;
    @include object-fit(scale-down); } }

// card gallery thumbs
.p-card-gallery-thumbs {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  justify-content: center;
  position: absolute;
  left: 0;
  top: 0;
  width: $gallery-thumbs-size;
  min-height: 100%;

  .swiper-container {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    height: $gallery-thumbs-height;
    width: 100%; }

  .swiper-wrapper {
    height: auto; } }

.p-card-gallery-thumbs__item {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  width: $gallery-thumbs-size;
  height: $gallery-thumbs-size;
  border: 1px solid $border-color;
  border-radius: 2px;
  // transition
  $prop: border-color;
  will-change: $prop;
  transition: {
    property: $prop;
    duration: $animation-speed; }
  user-select: none;
  -webkit-tap-highlight-color: transparent;

  &::after {
    content: "";
    display: block;
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    //background-color: $base-color
    @include linear-gradient(to top, black 0%, $base-color 100%);
    opacity: 0;
    // transition
    $prop: opacity;
    will-change: $prop;
    transition: {
        property: $prop;
        duration: $animation-speed; } }

  &.swiper-slide-thumb-active {
    //border: 1px solid rgba($base-color, 0.1)

    &::after {
      opacity: 0.12; } }

  img {
    display: block;
    max-width: calc(100% - 4px);
    max-height: calc(100% - 4px); } }

.p-card-gallery-thumbs__arrow-prev,
.p-card-gallery-thumbs__arrow-next {
  position: relative;
  height: 40px;
  //background-color: $base-color
  cursor: pointer;
  user-select: none;
  -webkit-tap-highlight-color: transparent;

  &::after {
    top: 50%;
    left: 50%;
    content: "";
    position: absolute;
    opacity: 1;
    pointer-events: none;
    @include size(12px);
    transform: rotate(45deg);
    border: 0 solid #303030;
    margin: -15px -10px; }
  &:focus {
    outline: none; }

  &.swiper-button-disabled {
    pointer-events: none;
    opacity: 0.5; } }

.p-card-gallery-thumbs__arrow-prev {
  &::after {
    border-width: 2px 0 0 2px;
    margin: -4px -5px; }

  &:active {
    top: -1px; } }

.p-card-gallery-thumbs__arrow-next {
  &::after {
    border-width: 0 2px 2px 0;
    margin: -9px -5px; }

  &:active {
    top: 1px; } }

// p-card-services
.p-card-services {
  overflow: hidden;

  .p-card__main & {
    padding-right: 5rem; } }

.p-card-services__list {
  display: flex;
  flex-wrap: wrap;
  margin: 0 -2rem; }

.p-card-services__item {
  flex-grow: 1;
  $width: 33.33%;
  flex-basis: $width;
  width: $width;
  padding: 0 2rem;
  font-size: 1.2rem;
  line-height: 1.9rem;
  color: $text-color-light;
  border-left: 2px solid $border-color-light;

  & > strong {
    display: block;
    position: relative;
    @include font-bold;
    font-size: 1.4rem;
    line-height: 1.8rem;
    color: #000;
    padding: 0.2rem 0 0.2rem 3.8rem;
    margin-bottom: 2rem;
    cursor: default;

    svg {
      $size: 2.4rem;
      display: inline-block;
      position: absolute;
      left: 0;
      top: 50%;
      width: $size;
      height: $size;
      margin-top: -#{round-float($size / 2, 1)};

      path {
        fill: $base-color; } } }

  p {
    margin: 0; }

  mark {
    background: none;
    color: $base-color; } }

// === product card aside ===
// ==========================
// card aside action
.p-card-action {
  padding: 0 #{nth($box-inner-space, 2)};
  margin-bottom: 0.7rem;
  text-align: right;
  cursor: default;

  &:last-child {
    margin-bottom: 0; } }

.p-card-action__btn {
  margin: 0 0 1rem 5rem; }

// card aside stock
.p-card-stock {
  position: relative;
  display: inline-block;
  vertical-align: top;
  padding: 0.3rem 0 0.3rem 2.8rem;
  flex-basis: 50%;
  font-size: 1.4rem;
  line-height: 1.6rem;
  color: #000;

  & > i {
    $size: 1.8rem;
    position: absolute;
    left: 0;
    top: 50%;
    width: $size;
    height: $size;
    background-color: #7ed321;
    border-radius: round-float($size / 2, 1);
    transform: translate(0, -50%);

    &::before {
      $size: 0.5rem 0.8rem;
      content: "";
      display: block;
      position: absolute;
      left: 50%;
      top: 50%;
      height: nth($size, 1);
      width: nth($size, 2);
      margin: -#{round-float(nth($size, 1)/2, 1) + 0.1} 0 0 -#{round-float(nth($size, 2)/2, 1)};
      border-style: solid;
      border-color: white;
      border-width: 0 0 1px 1px;
      transform: rotate(-45deg); } }

  // off
  &.off {
    color: $text-color-disabled;

    & > i {
      border-color: currentColor;

      &::before {
        transform: rotate(0deg);
        height: 0;
        margin-top: 0; } } } }

// code
.p-card-code {
  font-size: 1.4rem;
  line-height: 1.6rem;
  color: $text-color-soft; }

// price
.p-card-price {
  line-height: 1;

  // off
  &.off {
    color: $text-color-soft;

    .p-card-price__old,
    .p-card-price__new {
      color: currentColor; }

    & > svg path {
      fill: currentColor; } } }

.p-card-price__old,
.p-card-price__new {
  display: inline-block;
  vertical-align: baseline;
  line-height: 5.8rem; }

// price old
.p-card-price__old {
  color: $text-color-soft;
  margin-right: 0.4rem;

  & > strong, & > em {
    display: inline-block;
    vertical-align: baseline;
    @include font-normal;
    font-size: 1.8rem; }

  & > strong {
    position: relative;

    &::after {
      $size: 2em;
      content: "";
      display: block;
      position: absolute;
      top: 50%;
      left: 50%;
      width: $size;
      height: 0;
      margin: -0.1em -#{round-float($size / 2, 1)};
      border-top: 1px solid currentColor;
      transform: rotate(-42deg); } } }

// price new
.p-card-price__new {
  color: #000;

  & > strong, & > em {
    display: inline-block;
    vertical-align: baseline; }

  & > strong {
    @include font-bold;
    font-size: 4.5rem; }

  & > em {
    font-style: normal;
    font-size: 2.4rem; } }

// button buy
.btn-buy {
  position: relative;
  padding: 1.4rem 3.8rem;
  @include font-bold;
  @include font(1.8rem, 2.2rem);
  text-decoration: none;
  text-align: center;
  color: #000;
  background-color: $btn-bg-color;
  border: none;
  border-radius: 4px;
  box-shadow: 0 1px 1px rgba(0, 0, 0, 0.4);
  // transition
  $prop: background-color, color;
  transition: {
    property: $prop;
    duration: $animation-speed; }
  user-select: none;
  -webkit-tap-highlight-color: transparent;

  & > i {
    $size: 1.8rem;
    //display: none
    position: absolute;
    left: 1rem;
    top: 50%;
    height: $size;
    width: $size;
    opacity: 0;
    $translate: 10px, 0;
    transform: translate($translate);
    transform: translate3d($translate, 0);
    margin-top: -#{round-float($size / 2, 1) + 0.1};

    svg {
      display: inline-block;
      vertical-align: top;
      width: $size;
      height: $size;

      path {
        fill: $base-color;
        // transition
        $prop: fill, stroke;
        transition: {
          property: $prop;
          duration: $animation-speed; } } } }

  .btn-buy__in-cart {
    display: none; }

  // button buy states
  // hover
  &:hover {
    color: #000;
    box-shadow: 0 3px 6px rgba(black, 0.3);
    top: -1px; }

  // active
  &:active {
    top: 0;
    box-shadow: none; }

  // added
  &.added {
    .btn-buy__buy {
      display: none; }

    .btn-buy__in-cart {
      display: block; }

    & > i {
      background: none;
      box-shadow: none;
      border-radius: 0;
      opacity: 1;
      $translate: 0, 0;
      transform: translate($translate);
      transform: translate3d($translate, 0);
      // transition
      $prop: transform, opacity;
      will-change: $prop;
      transition: {
        property: $prop;
        duration: $animation-speed * 2;
        timing-function: cubic-bezier(0.18, 0.89, 0.32, 1.28); } } } }

.btn-buy_stretch {
  display: block; }

// options
.p-card-options {
  margin-bottom: 1rem;

  &:last-child {
    margin-bottom: 0; } }

.p-card-options__item {
  margin-top: 0.7rem;

  &:first-child {
    margin-top: 0; } }

// p-card-offer
.p-card-offer {
  position: relative;
  border: 1px solid $border-color-light;
  padding: 2rem; }

.p-card-offer__meta {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1rem;

  &:last-child {
    margin-bottom: 0; }

  .p-card-stock, .p-card-code {
    margin-bottom: 1rem; }

  .p-card-stock {
    padding-right: 1rem; } }

.p-card-offer__buy {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;

  //.p-card-price, .p-card-options
  //  flex-basis: 50%
  //  flex-grow: 1

  .p-card-price {
    flex-grow: 1;
    flex-basis: 50%;
    padding-right: 1rem; }

  .p-card-options {
    flex-basis: 50%;
    flex-grow: 1; }

  &:last-child {
    margin-bottom: 0; } }

// credit
.credit {
  position: relative; }

// thumbs
.credit-thumbs {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
  align-items: flex-start;
  cursor: default;
  user-select: none;
  -webkit-tap-highlight-color: transparent;

  a {
    position: relative;
    display: inline-block;
    vertical-align: top;
    text-decoration: none;
    color: #000;
    font-size: 1.3rem;
    line-height: 2.0rem;
    padding: 1rem 3.3rem 0.9rem 1.6rem;
    border-radius: 4px 4px 0 0;
    // transition
    $prop: background-color, color, box-shadow;
    will-change: $prop;
    transition: {
      property: $prop;
      duration: $animation-speed; }
    user-select: none;
    -webkit-tap-highlight-color: transparent;

    & > em {
      $size: 1.2rem;
      $arr-size: 0.4rem;
      position: absolute;
      right: 1.2rem;
      top: 50%;
      @include size($size);
      margin-top: -#{round-float($size / 2, 1)};
      border-radius: round-float($size / 2, 1);
      background-color: #d7d0ef;
      text-indent: -9999px;
      overflow: hidden;
      // transition
      $prop: transform;
      will-change: $prop;
      transition: {
        property: $prop;
        duration: $animation-speed; }

      &::before {
        content: "";
        display: block;
        position: absolute;
        left: 50%;
        top: 50%;
        height: $arr-size;
        width: $arr-size;
        margin: {}
        border: solid white;
        border-width: 0 0 1px 1px;
        margin: -0.3rem -0.2rem;
        transform: rotate(-45deg); } }

    // hover
    &:hover {
      background-color: #d7d0ef; }

    // active
    &:active {
      top: 1px; }

    // is open
    &.tabs-active {
      @include font-bold;
      background-color: #f9f7ff;
      pointer-events: none;

      & > em {
        transform: rotate(0.5turn);
        background-color: $base-color; } } } }

// panels
.credit-panel {
  font-size: 1.3rem;
  line-height: 1.9rem;
  color: $text-color-light;
  background-color: #f9f7ff;
  border: 2px solid #f9f7ff;
  border-radius: 0 0 1px 1px;

  p {
    margin-bottom: 1.9rem;

    &:last-child {
      margin-bottom: 0; } } }

.credit-panel__title {
  padding: 2.5rem 2rem 2rem;
  margin-bottom: 0; }

.credit-panel__content {
  background-color: white;
  padding: 2.2rem nth($box-inner-space, 2) 2rem; }

// credit row
.credit-info {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 2rem;

  &:last-child {
    margin-bottom: 0; }

  ul {
    list-style: none;
    padding: 0;
    margin: 0 0 1.9rem;

    li {
      position: relative;
      margin-top: 0.7rem;

      &:first-child {
        margin-top: 0; }

      &::before {
        content: '-';
        color: $base-color;
        margin-right: 5px; } }

    &:last-child {
      margin-bottom: 0; } } }

.credit-info__col {
  flex-grow: 1;
  flex-basis: 50%;

  [class*="btn"] {
    display: block; }

  &:first-child {
    padding-right: 2rem; } }

// card-info
.card-info {
  font-size: 1.4rem;
  line-height: 2.1rem;
  color: $text-color;
  margin-bottom: 2rem;

  &:last-child {
    margin-bottom: 0; } }

.card-info__title {
  display: block;
  @include font-bold;
  font-size: 1.8rem;
  line-height: 2.4rem; }

.card-info__content {
  strong {
    @include font-bold; }

  mark {
    color: $base-color;
    background: none; } }

// credit note
.credit-note {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 2rem;

  &:last-child {
