  flex-basis: 50%;

  &:first-child {
    padding-right: 2rem; } }

// aside
.p-card__aside {
  width: $card-cont-width;
  float: right;
  clear: right;
  padding-right: 0;
  margin-top: -0.4rem;

  // offer
  .p-card-offer {
    margin-bottom: 3.8rem;

    &:last-child {
      margin-bottom: 0; } } }

.cards-slider {
  padding: 0 2rem;

  .js &:not(.is-loaded) {
    opacity: 0;
    pointer-events: none; } }

$card-size: 85px 135px;
.cards-slider__slide {
  position: relative;
  height: nth($card-size, 1);
  width: round(nth($card-size, 2) * 0.7);
  margin-top: 2px;
  //border: solid transparent
  //border-width: 3px 1px
  box-sizing: content-box;
  $transform: 0, 32px;
  transform: translate($transform);
  transform: translate3d($transform, 0);
  cursor: default;
  @include clear; }

.cards-slider-figure {
  border-radius: 10px;
  float: left;
  overflow: hidden;
  height: nth($card-size, 1);
  max-width: round(nth($card-size, 2) * 0.7);
  $transform: 0, 0;
  transform: translate($transform);
  transform: translate3d($transform, 0);
  // transition
  $prop: box-shadow, transform, max-width;
  will-change: $prop;
  transition: {
    property: $prop;
    duration: $animation-speed; }

  .cards-slider__slide:last-child & {
    float: right; } }

.cards-slider-img {
  height: nth($card-size, 1);
  width: nth($card-size, 2);
  text-indent: -9999px;
  overflow: hidden;
  border-radius: 10px;
  background-color: $base-color;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover; }

.cards-slider__slide {
  // hover and active
  &:hover, &.current-card {
    z-index: 2;

    .cards-slider-figure {
      max-width: nth($card-size, 2);
      box-shadow: 0 6px 15px rgba(0, 0, 0, 0.5);
      position: relative;
      $transform: -5px, -16px;
      transform: translate($transform);
      transform: translate3d($transform, 0); } }

  &:hover {
    z-index: 3; }

  &.current-card {
    z-index: 2; } }

.cards-slider-nav {
  position: relative;
  height: 0;
  margin: 0 2rem;
  z-index: 5; }

.cards-slider__prev,
.cards-slider__next {
  $size: 38px;
  display: block;
  position: absolute;
  top: 50%;
  width: $size;
  height: $size;
  color: #c3c3c3;
  border-radius: 50%;
  margin-top: -#{round-float($size / 2, 0)};
  background-color: white;
  border: 1px solid currentColor;
  cursor: pointer;
  outline: none;
  user-select: none;
  text-line-through-color: transparent;
  // transition
  $prop: color;
  will-change: $prop;
  transition: {
    property: $prop;
    duration: $animation-speed; }

  & > span {
    @include hide-text; }

  svg {
    $size: 13px 19px;
    display: block;
    position: absolute;
    left: 50%;
    top: 50%;
    height: nth($size, 1);
    width: nth($size, 2);
    margin: -#{round-float(nth($size, 1) / 2, 0)} 0 0 -#{round-float(nth($size, 2) / 2, 0)};

    path {
      fill: currentColor; } }

  &:not(.swiper-button-disabled):not(:hover):focus {
    box-shadow: 0 0 1px 1px rgba(77, 144, 254, 1); }

  &:not(.swiper-button-disabled):hover {
    color: $base-color; }

  &:not(.swiper-button-disabled):active {
    top: calc(50% + 1px); }

  &.swiper-button-disabled {
    background-color: #f1f1f1;
    cursor: default;
    opacity: 0.3; } }

.cards-slider__prev {
  left: 0; }

.cards-slider__next {
  right: 0; }

/* product card end */

/* tape slider */
.tape-slider {
  position: relative;
  padding-top: 5.7rem;

  .js &:not(.is-loaded) {
    opacity: 0;
    pointer-events: none; }

  .product {
    background-color: white; } }

// arrows
$swiper-button-size: 14px, 20px;
.tape-slider__prev, .tape-slider__next {
  position: absolute;
  right: 0;
  top: 0;
  height: nth($swiper-button-size, 1);
  width: nth($swiper-button-size, 2);
  will-change: transform;
  cursor: pointer;
  // transition
  $prop: color;
  transition: {
    property: $prop;
    duration: $animation-speed; }

  & > span {
    @include hide-text; }

  svg {
    $size: 13px 19px;
    display: block;
    position: absolute;
    left: 50%;
    top: 50%;
    height: nth($size, 1);
    width: nth($size, 2);
    margin: -#{round-float(nth($size, 1) / 2, 0)} 0 0 -#{round-float(nth($size, 2) / 2, 0)};

    path {
      fill: currentColor; } }

  // hover
  &:not(.swiper-button-disabled):hover {
    color: $base-color; }

  // active
  &:not(.swiper-button-disabled):active {
    transform: translate(0, 1px); }

  // disabled
  &.swiper-button-disabled {
    pointer-events: auto;
    cursor: default;
    opacity: 0.3;
    outline: none; } }

// prev arrow
.tape-slider__prev {
  right: #{nth($swiper-button-size, 2) + 24};

  &::after {
    left: 0;
    border-right: none;
    transform-origin: left top;
    transform: rotate(-45deg); } }

// next arrow
.tape-slider__next {
  &::after {
    right: 0;
    border-left: none;
    transform: rotate(45deg);
    transform-origin: right top; } }

/* tape slider end */

/* product prompts */
.p-prompts {
  position: relative; }

.p-prompts__thumbs {
