/* news preview element */
$news-elem-inner: 2.7rem 2rem 2.1rem;
.news-elem {
  display: block;
  position: relative;
  text-decoration: none;
  color: inherit;
  background-color: white;
  box-shadow: inset 0 0 0 1px $border-color; }

.news-elem__figure {
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  height: 22.3rem; }

.news-elem__content {
  position: relative;
  padding: $news-elem-inner; }

.news-elem__category {
  @include truncate(100%);
  @include font-normal;
  font-size: 1.2rem;
  line-height: 1.6rem;
  color: $base-color;
  margin-bottom: 1.3rem; }

.news-elem__title {
  $line-height: 3.1rem;
  $line-length: 3;
  position: relative;
  overflow: hidden;
  height: $line-height * $line-length;
  @include font-bold;
  font-size: 2.2rem;
  line-height: $line-height;
  color: $title-color;
  margin-bottom: 0.6rem;
  transition: color $animation-speed;

  &:last-child {
    margin-bottom: 0; }

  @include truncateCover($line-height, white); }

.news-elem__date {
  display: block;
  font-style: normal;
  font-size: 1.2rem;
  line-height: 1.7rem;
  color: $text-color-soft;
  margin-bottom: 0.6rem;

  &:last-child {
    margin-bottom: 0; } }

.news-elem__text {
  $line-height: 2.2rem;
  $line-length: 2;
  position: relative;
  overflow: hidden;
  height: $line-height * $line-length;
  font-size: 1.5rem;
  line-height: $line-height;
  color: $text-color-light;
  margin-bottom: 0.75rem;

  &:last-child {
    margin-bottom: 0; }

  @include truncateCover($line-height, white); }

// hover
a.news-elem:hover {
  .news-elem__title {
    color: $base-color; } }

// alternative view
.news-elem_alt {
  box-shadow: none;

  .news-elem__figure {
    height: 46.6rem; }

  .news-elem__content {
    display: flex;
    flex-direction: column;
    align-content: flex-start;
    justify-content: flex-end;
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    padding: 4.8rem 4.5rem;
